<template>
  <div class="theSearch">
    <template v-if="status === 'loading'">
      loading...
    </template>
    <template v-if="status === 'success' && items">
      <div class="count">
        <span>{{ items.length }} results found
        </span>
        <span class="deco d-block">
          <template v-if="$route.params.limit">
            {{ $route.params.limit }}
          </template>
          <template v-else>20</template>
          results per page
        </span>
        <span class="deco d-block" v-if="$route.params.query">
          Searching: {{ $route.params.query.split('+').join(', ') }}
        </span>
      </div>
      <div class="list-of-items">
        <baseItem
          v-for="(item, itemIndex) in items"
          :key="itemIndex"
          :item="item"
        />
      </div>
    </template>
  </div>
</template>

<script>
import api from '@/api'
import baseItem from '@/components/baseItem'

export default {
  mixins: [ api ],
  components: {
    baseItem
  }
}
</script>

<style lang="scss">
.count{
  $color: rgb(150,150,150);
  display: block;
  color: $color;
  font-size: .9rem;
  margin: 1.25em auto;
  padding: 0 1.25em;
  text-align: center;
  &:after,&:before{
    content: '';
    display: block;
    height: 1px;
    background: fade-out($color, .8);
    margin: 1.25rem auto;
    width: 20%;
    @include rpsd{
      width: 50%;
    }
  }
  .deco{
    font-size: 12px;
    color: fade-out($color, .5);
  }
}
.query{
  span{
    display: inline-block;
    background: transparent;
    color: default(color);
    border: 1px solid default(color);
    margin-right: .8em;
    line-heigt: 1em;
    padding: .5em 1.25em;
    border-radius: 9em;
  }
}
</style>



