<template>
  <div class="list-of-items filter">
    <div class="container">
      <div class="title">Filter</div>
      <form
        class="row"
        @submit.prevent="filterAction"
      >
        <div class="col-12 col-sm-6 col-lg-4">
          <input type="text" name="keywords" placeholder="Keyword">
        </div>
        <div class="col-12 col-sm-6 col-lg-3">
          <select name="district">
            <option value="" selected>------</option>
            <option
              v-for="(item, itemIndex) in district"
              :key="itemIndex"
              :value="item"
            >{{ item }}</option>
          </select>
        </div>

        <div class="col-12 col-sm-6 col-lg-3">
          <input
            name="charge"
            type="checkbox"
            id="charge"
            value="false"
            @click="toggleValue"
          >
          <label for="charge">IS FREE</label>

          <input
            name="openAllTime"
            type="checkbox"
            id="openAllTime"
            value="false"
            @click="toggleValue"
          >
          <label for="openAllTime">24 hr</label>
        </div>
        <div class="col-12 col-sm-6 col-lg-2">
          <button type="submit">SEARCH</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import baseItem from '@/components/baseItem'

import $ from 'jquery'

export default {
  mixins: [ api ],
  components: {
    baseItem
  },
  methods: {
    toggleValue: function (el) {
      const theTarget = $(el.target)
      var theVal = (theTarget.val() === 'true')
      theTarget.val(!theVal)
    },
    checkVal: function (el) {
      if (el !== null && el !== '' && el !== 'false') {
        return true
      } else {
        return false
      }
    },
    filterAction: function ($target) {
      const rKeyword = $target.target.keywords.value
      const rDistrict = $target.target.district.value
      const rCharge = $target.target.charge.value
      const rOpenAllTime = $target.target.openAllTime.value

      var theArray = []
      if (this.checkVal(rKeyword)) {
        theArray.push(rKeyword)
      }
      if (this.checkVal(rDistrict)) {
        theArray.push(rDistrict)
      }
      if (this.checkVal(rCharge)) {
        theArray.push('免費')
      }
      if (this.checkVal(rOpenAllTime)) {
        theArray.push('24小時')
      }
      if (theArray.length > 0) {
        this.$router.push({
          name: 'search',
          params: {
            query: theArray.join('+'),
            limit: 20
          }
        })
      } else {
        this.$router.push({
          name: 'all',
          params: {0: 0}
        })
      }
    }
  },
  data () {
    return {
      params: {
        charge: false
      },
      district: [
        '前金區',
        '新興區',
        '鹽埕區',
        '左營區',
        '楠梓區',
        '鼓山區',
        '旗津區',
        '苓雅區',
        '三民區',
        '前鎮區',
        '小港區',
        '鳳山區',
        '鳥松區',
        '大社區',
        '仁武區',
        '大樹區',
        '岡山區',
        '燕巢區',
        '梓官區',
        '永安區',
        '彌陀區',
        '橋頭區',
        '田寮區',
        '茄萣區',
        '阿蓮區',
        '路竹區',
        '湖內區',
        '那瑪夏鄉',
        '桃源區',
        '茂林區',
        '六龜區',
        '美濃區',
        '旗山區',
        '甲仙區',
        '內門區',
        '杉林區',
        '林園區',
        '大寮區'
      ]
    }
  }
}
</script>

<style lang="scss">
.filter{
  .row{
    >*{
      display: flex;
      justify-content: center;
      aling-items: center;
      min-height: 2.5em;
      margin-bottom: .8em;
    }
  }
  font-size: .9em;
  input[type="text"]{
    display: block;
    width: 100%;
    height: 100%;
    border: 1px solid fade-out(default(color), .5);
    background: transparent;
    padding: .5em 1.25em;
  }
  select{
    display: block;
    width: 100%;
    height: 100%;
  }
  button[type="submit"]{
    cursor: pointer;
    display: flex;
    justify-content: center;
    aling-items: center;
    width: 100%;
    border-radius: 99em;
    background: transparent;
    border: 2px solid default(color);
    color: default(color);
    font-weight: bold;
  }
  input[type="checkbox"]{
    position: relative;
    display: inline-block;
    width: 0;
    height: 0;
    @include opacity(0);
    z-index: -1;
    + label{
      line-height: 1em;
      cursor: pointer;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: .8em 1.25em;
      border-radius: 100em;
      color: default(color);
      border: 1px solid default(color);
      @include transition;
      margin-right: .5em;
    }
    &:checked + label{
      color: white;
      background: default(color);
    }
  }
}
</style>



