<script>
import axios from 'axios'
import Vue from 'vue'
import VueAxios from 'vue-axios'

Vue.use(VueAxios, axios)

export default {
  data () {
    return {
      items: null,
      status: null
    }
  },
  mounted: function () {
    this.grab()
  },
  methods: {
    grab: function () {
      const _self = this
      const apiMainURI = 'https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97'
      let pQuery = this.$route.params.query
      let pLimit = this.$route.params.limit

      let apiURI = apiMainURI

      var limits = 0
      if (pLimit && pLimit !== '' && pLimit !== null && Number(pLimit) > 0) {
        limits = Number(limits)
      }
      if (Number(limits) <= 0) {
        limits = 20
      }
      apiURI += '&limit=' + Number(limits)

      if (pQuery && pQuery !== '' && pQuery !== null) {
        apiURI += '&q=' + pQuery.toString()
      }
      console.log(apiURI)
      // status loading
      _self.status = 'loading'

      this.$http.get(apiURI)
        .then(function (response) {
          _self.items = response['data']['result']['records']
          _self.status = 'success'
        })
        .catch(function (error) {
          console.log(error)
          _self.status = 'error'
          _self.items = error
        })
    }
  },
  watch: {
    '$route.params': function () {
      this.grab()
    }
  }
}
</script>



