import Vue from 'vue'
import Router from 'vue-router'

import appHome from '@/views/appHome'
import theSearch from '@/components/theSearch'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: appHome,
      children: [
        {
          name: 'search',
          path: 'query/:query/:limit(\\d+)',
          component: theSearch
        },
        {
          name: 'all',
          path: '*',
          component: theSearch
        }
      ]
    }
  ]
})



