<template>
  <div class="baseItem card">
    <div class="block content">
      <main>
        <div class="row">
          <div class="col-12 inner head">
            {{ item.Name }}
          </div>
          <div class="col-12 col-lg-11 inner manifestro">
            <div class="cnt">
              <div class="info-block row">
                <div class="col-4 item">
                  <span data-title="zone">{{ item.Zone }}</span>
                </div>
                <div class="col-4 item">
                  <span data-title="price" v-if="item.Ticketinfo != ''">{{ item.Ticketinfo }}</span>
                  <span data-title="price" v-else>-</span>
                </div>
                <div class="col-4 item">
                  <span data-title="website" v-if="item.Website != ''">
                    <a :href="item.Website" target="_blank">Link</a>
                  </span>
                  <span data-title="website" v-else>-</span>
                </div>
                <div class="col-6 item">
                  <span data-title="Address">{{ item.Add }}</span>
                </div>
                <div class="col-6 item">
                  <span data-title="Tel">{{ item.Tel }}</span>
                </div>
                <div class="col-12 item">
                  <span data-title="openhour">{{ item.Opentime }}</span>
                </div>
              </div>

            </div>
          </div>
          <div class="col-12 col-lg inner info">
            <span class="m-font barCode">{{ item.Id }}</span>
          </div>
        </div>
      </main>
    </div>
    <div class="block theme-image">
      <figure
        :style="{'background-image': 'url(' + item.Picture1 + ')'}"
      ></figure>
    </div>
  </div>
</template>

<script>
export default {
  props: [ 'item' ]
}
</script>

<style lang="scss">
.baseItem{
  position: relative;
  margin-bottom: 1.25em;
  overflow: hidden;
  border-radius: 1em;
  width: 100%;
  @include rpsu{
    height: 0;
    padding-bottom: 38%;
  }
  @include rpsd{
    height: 100%;
  }

  .block{
    &.theme-image{
      position: absolute;
      z-index: 0;
      display: block;
      width: 100%;
      height: 100%;
      figure{
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        @include opacity(.3);
      }
    }
    &.content{
      @include rpsu{
        position: absolute;
      }
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      z-index: 5;
      main{
        // background: green;
        height: 100%;
        >.row{
          height: 100%;
          .inner{
            &.head{
              height: 13%;
              background: default(color);
              padding: .5em 1.5em;
              color: white;
              font-size: 1.5em;
            }
            &.manifestro{
              // background: red;
              display: flex;
              width: 100%;
              height: 100%;
              justify-content: flex-start;
              align-items: flex-start;
              padding-top: 1.25rem;
              .cnt{
                display: block;
                width: 100%;
                @include rpsu{
                  padding-left: 1.25em;
                }
                @include rpsd{
                  padding: 1.25rem;
                }
                .info-block{
                  .item{
                    margin-bottom: 1.25rem;
                    a{
                      font-size: .9em;
                      color: black;
                      text-decoration: underline;
                      letter-spacing: 0;
                      font-family: 'arial' !important;
                    }
                    &:last-child{
                      margin-bottom: 0;
                    }
                    span{
                      display: block;
                      padding: .6em 1.25em;
                      width: 100%;
                      height: 100%;
                      background: rgba(255,255,255,.9);
                      max-height: 7.4em;
                      overflow: hidden;
                      padding-left: 1.8em;
                      // text-align: right;
                      *{
                        line-height: 1em;
                      }
                      &:before{
                        text-align: left;
                        margin-left: -.6em;
                        content: attr(data-title);
                        display: block;
                        color: default(color);
                        font-size: 12px;
                        text-transform: uppercase;
                      }
                    }
                  }
                }
              }
            }
            &.info{
              position: relative;
              margin: 1.25rem;
              @include rpsu{
                margin-left: 0;
              }
              @include rpsd{
                margin-top: 0;
              }
              >[class*="col"]{
                position: relative
              }
              span.barCode{
                display: inline-block;
                position: relative;
                oveflow: hidden;
                @include rpsu{
                  position: absolute;
                  top: 0;
                  left: 0;
                  writing-mode: vertical-rl;
                  text-orientation: upright;
                  font-size: 2.8em;
                }
                @include rpsd{
                  padding: .45em;
                  max-height: 1em;
                  text-align: right;
                  font-size: 2.5em;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>



