<template>
  <div id="app">
    <theHeader />
    <main class="main">
      <router-view />
    </main>
    <theFooter />
  </div>
</template>

<script>
import theHeader from '@/components/theHeader'
import theFooter from '@/components/theFooter'

export default {
  name: 'App',
  components: {
    theHeader,
    theFooter
  }
}
</script>

<style lang="scss">
@import './assets/sass/main.scss';

html, body , #app{
  height: 100%;
  padding: 0;
  margin: 0;
}
#app{
  width: 100%;
  height: 100%;
  display: table;
  table-layout:fixed;
  header.header, footer.footer, main.main{
    display: table-row;
    width: 100%;
  }
  header.header, footer,footer{
    height: 1px;
  }

  background: rgba(0,0,0,.05);
  main.main{
    height: auto;
    .container{
      padding-top: 1.25em;
      padding-bottom: 1.25em;
    }
  }
}
</style>



